    animation: gslideOutLeft .3s ease;
  }
  
  .gslideInLeft {
    -webkit-animation: gslideInLeft .3s ease;
    animation: gslideInLeft .3s ease;
  }
  
  .gslideOutRight {
    -webkit-animation: gslideOutRight .3s ease;
    animation: gslideOutRight .3s ease;
  }
  
